.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
h1, h2, h3, h4, h5, h6 {
  text-transform: uppercase;
}
.padding {
  padding-top: 50px;
  padding-bottom: 50px;
}
.padding-sm {
  padding-top: 25px;
  padding-bottom: 25px;
}
.padding-lg {
  padding-top: 100px;
  padding-bottom: 100px;
}
.job {
  margin-bottom: 25px;
  padding-bottom: 25px;
  padding-top: 25px;
  border-bottom: 1px solid #e6e6e6;
}
.job:last-of-type {
    border-bottom: none;
    padding-bottom: 0;
}

























































































































































